:root {
  --fc-small-font-size: 0.85em;
  --fc-page-bg-color: #ffffff;
  --fc-neutral-bg-color: rgba(208, 208, 208, 0.3);
  --fc-neutral-text-color: #808080;
  --fc-border-color: #ddd;

  --fc-button-text-color: #ffffff;
  --fc-button-bg-color: #2196f3;
  --fc-button-border-color: #2196f3;
  --fc-button-hover-bg-color: #1976d2;
  --fc-button-hover-border-color: #1976d2;
  --fc-button-active-bg-color: #90caf9;
  --fc-button-active-border-color: #90caf9;

  --fc-event-bg-color: #2196f3;
  --fc-event-border-color: #2196f3;
  --fc-event-text-color: #ffffff;
  --fc-event-selected-overlay-color: rgba(0, 0, 0, 0.25);

  --fc-event-resizer-thickness: 8px;
  --fc-event-resizer-dot-total-width: 8px;
  --fc-event-resizer-dot-border-width: 1px;

  --fc-more-link-bg-color: #d0d0d0;
  --fc-more-link-text-color: inherit;

  --fc-list-event-dot-width: 10px;
  --fc-list-event-hover-bg-color: #f5f5f5;

  --fc-non-business-color: rgba(215, 215, 215, 0.3);
  --fc-bg-event-color: rgb(143, 223, 130);
  --fc-bg-event-opacity: 0.3;
  --fc-highlight-color: rgba(188, 232, 241, 0.3);
  --fc-today-bg-color: rgba(255, 220, 40, 0.15);
  --fc-now-indicator-color: #e53935;
}
